<template>
  <div class="join-wrapper">

    <app-bg
        :height="223" src="join/2_02.jpg"/>
    <app-bg
        :height="137" src="join/2_03.jpg"/>

        <div class="form-box">
            <div class="form-body">
                 <div class="form-types">
                    <ul class="type-list f-row-between">
                        <li class="type-item f-pr" :style="{backgroundColor: msgtype === o.value ? '#e7decf' : '#eee'}" v-for="(o, i) in typelist" :key="i">{{o.label}}<input type="radio" class="type-item-radio" :value="o.value" :id="o.value" v-model="msgtype"/></li>
                    </ul>
                 </div>
                 <div class="question-box">
                    <textarea class="form-textarea" placeholder="在这里输出您想知道的问题，我们将尽快与您联系！" v-model="question"></textarea>
                 </div>
                 <div class="form-row f-row-between">
                      <input type="text" style="margin-right: 10px;"  v-model="name" placeholder="姓名" class="form-input" />
                      <input type="text"  v-model="phone" placeholder="手机" class="form-input" />
                 </div>
                 <div class="submit-btn-wrap f-row-center">
                     <button type="button" name="button" @click="submit">提交留言</button>
                 </div>
            </div>
        </div>


    <app-bg
        :height="258" src="join/2_05.jpg"/>

    <app-bg
        :height="284" src="join/2_06.jpg"/>

    <app-bg
        :height="331" src="join/2_07.jpg"/>


    <app-bg
        :height="30" src="join/2_08.jpg"/>


    <app-bg
        :height="750" src="join/2_09.jpg"/>


    <app-bg
        :height="340" src="join/2_10.jpg"/>


    <app-bg
        :height="357" src="join/2_11.jpg"/>

    <app-bg
        :height="262" src="join/2_12.jpg"/>
    <app-bg
        :height="292" src="join/2_13.jpg"/>

  </div>
</template>

<script>
import AppBg from '@/components/AppBg'
import Bus from '@/bus'
export default {
  data() {
    return {
      name: '',
      phone: '',
      question: '',
      msgtype: '10',
      submiting: false,
      typelist: Bus.msg_typelist
    }
  },
  name: 'join',
  created() {
  },
  mounted() {
  },
  methods: {
    trim(str = '') {
      if (typeof str === 'string') {
        return str.replace(/(^\s*)|(\s*)$/g, '')
      }
    },
    verifyParams() {
      if (this.trim(this.name) === '') {
        alert('请输入姓名')
        return false
      } else if (this.trim(this.phone) === '') {
        alert('请输入联系方式')
        return false
      }
      if (this.name.length > 8) {
        alert('姓名长度为1-8')
        return false
      } else if (!/^1[34578]\d{9}$/.test(this.phone)) {
        alert('联系方式格式有误')
        return false
      }
      return true
    },
    submit() {
      if(!this.verifyParams()) return
      const params = {
        name: this.name,
        phone: this.phone,
        type: this.msgtype,
        message: this.question
      }
      const self = this
      if (this.submiting) return
      this.submiting = true
      this.$http.post('/unauth/add', params)
        .then(function (response) {
          self.submiting = false
          if (response.data.success) {
            self.resetValue()
            alert('提交成功！')
          }
        })
        .catch(err => {
          self.submiting = false
          console.log(err)
        })
    }
  },
  components: {
    AppBg
  }
}
</script>

<style scoped lang="less">
.join-wrapper{
  margin-top: -12px;
  .form-box{
      width: 100%;
      padding: 20px 10px;
      background-color: #f7f6f1;
  }
  .form-body{
    padding: 10px;
    width: 100%;
    height: 100%;
    border: 1px solid #7a7977;
  }
  .form-types{

  }
  .type-list{
    flex-wrap: wrap;
  }
  .type-item{
    font-size: 0.16rem;
    color: #625e5b;
    font-weight: 700;
    background-color: #e7decf;
    border-radius: 4px;
    padding: 6px 20px;
    margin-bottom: 10px;
    position: relative;
  }
  .type-item-radio{
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    position: absolute;
    z-index: 20;
    opacity: 0;
  }
  .question-box{
    width: 100%;
  }
  input[type=text]{
    flex: 1;
    height: 0.36rem;
    font-size: 0.12rem;
    border-radius: 5px;
    padding-left: 0.05rem;
    background-color: #f7f6f1;
    border: 1px solid #93928e;
  }
  .form-textarea{
      width: 100%;
      font-size: 0.12rem;
      padding: 0.1rem;
      border: 1px solid #93928e;
      border-radius: 5px;
      background-color: #f7f6f1;
      height: 0.8rem;
  }
  .form-row{
    margin-top: 6px;
  }
  .submit-btn-wrap{
    margin-top: 10px;
    button{
      width: 100%;
      font-size: 0.15rem;
      color: #595554;
      background-color: #e6ddce;
      border:0;
      padding: 0.06rem;
      border-radius: 5px;
    }
  }
}
</style>



